
$icon-size: 19px;

.btn {
  background: white;
  padding: 1.4rem 2rem;
  border-radius: 30px;
  box-shadow: 0px 0px 35px rgba(0, 0, 0, 0.19);
  font-family: $noto;
  font-size: 1rem;
  font-weight: 300;
  text-decoration: none;
  color: $blue-dark;
  display: inline-block;
  font-style: italic;
  @include position;
  @include transition(all, 0.4s);

  &:hover {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
  }

  &--small {
    padding: 1rem 2rem 1rem 2.6rem;
  }

  .icon {
    @include position(absolute, 1rem, 50%);
    width: $icon-size;
    height: $icon-size;
    transform: translateY(-9px);

    svg {
      width: 100%;
      height: 100%;
    }
  }

  .text {
    @include position;
    top: 1px;
  }
}

.btn--light {
  box-shadow: 0px 0px 35px rgba(0, 0, 0, 0.05);
}